  font-weight: 500;
  line-height: 1.2;
  margin-bottom: 0.5rem;
}
.form .form-submit {
  background-color: #fd854c;
  color: white;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  padding: 13px 20px;
  border: 1px solid #fd854c;
  border-radius: 0.3rem;
  font-weight: 600;
  font-size: 16px;
}
.form .form-other {
  font-size: 80%;
  font-weight: 400;
  margin-top: 0.75rem;
  margin-bottom: 1rem;
}
.form > * {
  width: 80%;
}

.input-group {
  -ms-flex-align: stretch;
      align-items: stretch;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
